"use client"

import type React from "react"

import { useEffect, useState, useRef } from "react"
import { Loader2 } from "lucide-react"

export const ApiTerminal = () => {
  const [logs, setLogs] = useState(["> System initialized...", "> Waiting for auth..."])
  const [formData, setFormData] = useState({ username: "", password: "" })
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)
  const logsEndRef = useRef<HTMLDivElement>(null)

  const addLog = (msg: string) => {
    setLogs((prev) => [...prev, `> ${msg}`])
  }

  useEffect(() => {
    logsEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [logs])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    if (!formData.username || !formData.password) {
      addLog("ERROR: Credentials missing.")
      return
    }

    setLoading(true)
    addLog(`Connecting to secure endpoint...`)

    setTimeout(() => {
      setLoading(false)
      setSuccess(true)
      addLog("STATUS: 200 OK")
      addLog("Token received: eyJhbGciOiJIUzI1Ni...")
      addLog("ACCESS GRANTED. Welcome aboard.")
    }, 2000)
  }

  return (
    <section className="min-h-screen py-24 px-6 md:px-20 bg-[#050505] flex flex-col items-center justify-center relative overflow-hidden border-t border-gray-800">
      <div className="z-10 w-full max-w-5xl">
        <h2 className="text-4xl md:text-6xl font-black text-white mb-8 text-center md:text-left">
          <span className="text-(--color-primary)">{`{ `}</span>
          API CHALLENGE
          <span className="text-(--color-primary)">{` }`}</span>
        </h2>

        <div className="grid grid-cols-1 md:grid-cols-2 border border-gray-800 bg-[#0a0a0a] rounded-xl overflow-hidden shadow-2xl">
          <div className="p-8 md:p-12 border-b md:border-b-0 md:border-r border-gray-800">
            <form onSubmit={handleSubmit} className="space-y-6">
              <div>
                <label className="text-(--color-primary) font-mono text-xs uppercase mb-2 block">Username</label>
                <input
                  type="text"
                  className="w-full bg-[#111] border border-gray-700 p-4 text-white focus:outline-none focus:border-(--color-primary) font-mono rounded transition-colors"
                  placeholder="root"
                  value={formData.username}
                  onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                  disabled={success}
                />
              </div>
              <div>
                <label className="text-(--color-primary) font-mono text-xs uppercase mb-2 block">Password</label>
                <input
                  type="password"
                  className="w-full bg-[#111] border border-gray-700 p-4 text-white focus:outline-none focus:border-(--color-primary) font-mono rounded transition-colors"
                  placeholder="•••••••"
                  value={formData.password}
                  onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                  disabled={success}
                />
              </div>
              <button
                type="submit"
                disabled={loading || success}
                className={`w-full py-4 font-bold uppercase font-mono tracking-wider transition-all hover:brightness-110 flex items-center justify-center gap-2 ${
                  success ? "bg-green-600 text-black" : "bg-(--color-primary) text-black"
                } disabled:opacity-50`}
              >
                {loading && <Loader2 size={20} className="animate-spin" />}
                {loading ? "Transmitting..." : success ? "Connected" : "Login"}
              </button>
            </form>
          </div>

          <div className="bg-black p-6 font-mono text-xs text-gray-400 h-[300px] md:h-auto overflow-y-auto">
            {logs.map((log, i) => (
              <div key={i} className="mb-2 break-all text-[11px]">
                {log}
              </div>
            ))}
            <div ref={logsEndRef} />
            {loading && <div className="animate-pulse-custom text-(--color-primary)">_</div>}
          </div>
        </div>
      </div>
    </section>
  )
}
